@use "sass:math";
@use "@ezycore/ui/assets/styles/tokens/breakpoints" as *;
@use "@ezycore/ui/assets/styles/tools/media" as *;

// Nmero de columnas del sistema
$cols-count: 12;

// ===============================
// .row
// ===============================
.row {
  display: flex;
  flex-wrap: wrap;
  // SIN padding para que uses gap-* como gutter
  // Ejemplo: <div class="row gap-4">...</div>
}

// ===============================
// .col (auto)  se reparte el espacio
// ===============================
.col {
  flex: 1 0 0; // columnas flexibles que se reparten el ancho disponible
  min-width: 0;
}

// ===============================
// .col-1 ... .col-12
// ===============================
// Sin padding, solo width. Los gutters vienen de gap-*
@for $i from 1 through $cols-count {
  .col-#{$i} {
    // flex-basis + width para evitar bugs raros
    flex: 0 0 auto;
    width: math.div(100% * $i, $cols-count);
    max-width: math.div(100% * $i, $cols-count);
  }
}

// ===============================
// .col-auto  tamao segn contenido
// ===============================
.col-auto {
  flex: 0 0 auto;
  width: auto;
}

// ===============================
// Responsive: .col-sm-6, .col-md-4, etc.
// ===============================
@each $bp-name, $bp-value in $breakpoints {
  @if $bp-value != 0 {
    @include up($bp-name) {
      // col-{bp}  auto
      .col-#{$bp-name} {
        flex: 1 0 0;
        min-width: 0;
      }

      // col-{bp}-1 ... col-{bp}-12
      @for $i from 1 through $cols-count {
        .col-#{$bp-name}-#{$i} {
          flex: 0 0 auto;
          width: math.div(100% * $i, $cols-count);
          max-width: math.div(100% * $i, $cols-count);
        }
      }

      // col-{bp}-auto
      .col-#{$bp-name}-auto {
        flex: 0 0 auto;
        width: auto;
      }
    }
  }
}
